import { profileAPI } from "../api/api";

const ADD_POST = "ADD-POST";
const DELETE_POST = "DELETE-POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const initialstate = {
  profile: null,
  posts: [
    { message: "sfhleg", id: 1 },
    { message: "lesfgsffg", id: 2 },
    { message: "fe sfgs dg", id: 3 },
    { message: "ae fgsg fsgg", id: 4 },
  ],
  status: "",
};
let profileReducer = (state = initialstate, action) => {
  switch (action.type) {
    case ADD_POST:
      let newPost = { message: action.newPostText, id: 3 };
      let newState = { ...state, posts: [...state.posts] };
      newState.posts.push(newPost);
      return newState;
    case DELETE_POST:
      return {
        ...state,
        posts: state.posts.filter((p) => p.id != action.postId),
      };

    case SET_USER_PROFILE:
      return { ...state, profile: { ...action.profile } };
    case SET_STATUS:
      return { ...state, status: action.status };

    default:
      return state;
  }
};
export let addPost = (newPostText) => ({ type: ADD_POST, newPostText });
export let deletePost = (postId) => ({ type: DELETE_POST, postId });
export let getUserProfile = (id) => async (dispatch) => {
  let data = await profileAPI.getProfile(id);
  dispatch(setUserProfile(data));
  // .catch(() =>
  //   dispatch(setUserProfile({ name: "Oleg Dub" }))
  // );
};
export let getUserStatus = (id) => async (dispatch) => {
  let status = await profileAPI.getStatus(id);
  dispatch(setStatus(status));
  // .catch(() => dispatch(setStatus("I am very cool")));
};
export let updateStatus = (status) => async (dispatch) => {
  let data = await profileAPI.updateStatus(status);
  if (data.resultCode === 0) {
    dispatch(setStatus(status));
  }
  // .catch(() => dispatch(setStatus(status)));
};

export let setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile });
export let setStatus = (status) => ({ type: SET_STATUS, status });
export default profileReducer;
