import styles from "./Pagination.module.css";
import React, { useState } from "react";
const Pagination = ({
  totalUsersCount,
  pageSize = 5,
  currentPage = 1,
  onPageChanged,
  portionSize = 10,
}) => {
  let pages = [];
  let pagesCount = Math.ceil(totalUsersCount / pageSize);
  let portionCount = Math.ceil(pagesCount / portionSize);
  let [portionNumber, setPortionNumber] = useState(1);
  let leftBorderPortionSize = (portionNumber - 1) * portionSize + 1;
  let rightBorderPortionSize = portionSize * portionNumber;
  for (let i = 1; i <= pagesCount; i++) {
    pages.push(i);
  }
  let pagesElements = pages
    .filter((p) => p >= leftBorderPortionSize && p <= rightBorderPortionSize)
    .map((p) => (
      <span
        key={p}
        className={currentPage === p && styles.selected}
        onClick={(e) => onPageChanged(p)}
      >
        {p}
      </span>
    ));
  return (
    <div>
      {portionNumber > 1 && (
        <button onClick={() => setPortionNumber(portionNumber - 1)}>
          Prev
        </button>
      )}
      {pagesElements}
      {portionNumber < portionCount && (
        <button onClick={() => setPortionNumber(portionNumber + 1)}>
          Next
        </button>
      )}
    </div>
  );
};
export default Pagination;
