const SEND_MESSAGE = "SEND-MESSAGE";
const initialstate = {
  dialogs: [
    { name: "oleg", id: 1 },
    { name: "leg", id: 2 },
    { name: "feg", id: 1 },
    { name: "aeg", id: 2 },
  ],
  messages: [
    { message: "ofgdleg", id: 1 },
    { message: "lefffg", id: 2 },
    { message: "feg", id: 1 },
    { message: "aeg", id: 2 },
  ],
};
let dialogReducer = (state = initialstate, action) => {
  switch (action.type) {
    case SEND_MESSAGE:
      let a = action.newMessageText;
      return {
        ...state,
        messages: [...state.messages, { message: a, id: 3 }],
      };

    default:
      return state;
  }
};
export let sendMessage = (newMessageText) => ({
  type: SEND_MESSAGE,
  newMessageText,
});

export default dialogReducer;
